                      @input="updateMandatory($event, field, index)"
                      @update:freezedData="updateFreezedData"
                      @update:freezed="updateFreezeState"
                      @update:removeFreezed="removeFreezedData"
                    />
                    <BotRecommendations
                      v-if="
                        fieldType === 'STATIC' &&
