              <Tabs v-model="tab" :tabs="tabsFields" />
            </template>
          </div>
          <template v-if="tab === 'FIELDS'">
            <ValidationObserver ref="form">
              <div class="row items-center q-mb-sm">
                <div class="col q-mr-sm">
                  <div @click="addField">
                    <q-chip
