                </template>
              </div>
            </ValidationObserver>
          </template>
          <template v-if="tab === 'TABLE' && dynamicData.length">
            <div id="dynamicIndexingTables">
              <div class="row q-mb-sm q-mt-sm">
                <BaseActionButton
                  icon="eva-external-link-outline"
