      if (error) {
        this.$alert.error(error);
        return;
      }

      if (!payload) {
        return;
      }
      //console.log(payload);

      this.fieldType = payload.fieldsType;
      this.repositoryFieldsData = payload;
      this.repositoryFields = payload;
    },

